div.ov_color_circle {
	background: #ffffff;
	border: 1px solid #000000;
	width: 14px;
	height: 14px;
	display: inline-block;
	margin-right: 8px;
	margin-bottom: -2px;
	border-radius: 10px;
}

div.header {
	overflow: auto;
	display: none;
}

div.title {
	padding: 6px 10px;
	overflow: auto;
}

div.title div.title_left {
	float: left;
}

div.title svg.logo_image {
	width: 190px;
	height: 40px;
	float: left;
}

div.title div.logo_text {
	color: var(--ov_foreground_color);
	font-size: 18px;
	font-weight: bold;
	padding: 9px;
	float: left;
}

div.title div.title_right {
	float: right;
}

div.title_right a {
	color: var(--ov_foreground_color);
	padding: 11px 5px;
	display: block;
	float: left;
	text-decoration: none;
}

div.toolbar {
	background: var(--kreacad_toolbar_background_color);
}

div.intro {
	margin: 10px;
	padding: 10px;
	text-align: center;
	border: 2px dashed var(--ov_border_color);
	overflow: auto;
	display: none;
}

div.intro_content {
	width: 500px;
	max-width: 90%;
	margin: 10px auto;
	position: relative;
}

div.intro div.intro_logo {
	border-bottom: 1px solid var(--ov_border_color);
	padding-bottom: 30px;
	margin-bottom: 30px;
}

div.intro svg.intro_logo {
	width: 381px;
	height: 80px;
	max-width: 90%;
	margin-bottom: 20px;
}

div.intro div.intro_dragdrop_text {
	font-size: 30px;
}

div.intro div.intro_formats_title {
	font-size: 25px;
	margin-bottom: 15px;
}

div.intro div.intro_file_formats {
	margin: 0px auto;
}

div.intro div.intro_file_formats a {
	color: var(--ov_outline_button_text_color);
	text-decoration: none;
	font-size: 17px;
	width: 50px;
	border-radius: 5px;
	padding: 4px 8px;
	margin: 6px 4px;
	border: 1px solid var(--ov_outline_button_color);
	display: inline-block;
	cursor: pointer;
}

div.intro div.intro_file_formats a:hover {
	background: var(--ov_outline_button_hover_color);
}

div.noembed {
	text-align: center;
	padding: 10px;
}

div.noembed a {
	padding: 10px 0px;
	display: block;
}

div.main {
	overflow: hidden;
	display: none;
}

div.main_file_name {
	margin: 10px auto;
	white-space: nowrap;
	text-align: center;
	text-overflow: ellipsis;
	overflow: hidden;
}

div.main_left_container {
	float: left;
	overflow: auto;
}

div.main_navigator {
	width: 280px;
	margin: 10px 0px 10px 0px;
	overflow: none;
	float: left;
}

div.main_splitter {
	width: 10px;
	overflow: none;
	float: left;
	cursor: w-resize;
}

div.main_viewer {
	float: left;
}

div.main_right_container {
	float: left;
	overflow: auto;
}

div.main_sidebar {
	width: 280px;
	margin: 10px 0px 10px 0px;
	overflow: none;
	float: left;
}

div.main_viewer canvas {
	margin: 10px 0px 10px 0px;
	border: 1px solid var(--ov_border_color);
	outline: none;
	display: block;
}

div.kreacad_toolbar {
	overflow: auto;
	user-select: none;
}

div.kreacad_toolbar div.kreacad_toolbar_button {
	float: left;
	cursor: pointer;
	padding: 10px;
	transition: all 0.3s ease;
}

div.kreacad_toolbar div.kreacad_toolbar_button:hover {
	animation: pulse 1s;
}

div.kreacad_toolbar div.kreacad_toolbar_button.align_right {
	float: right;
}

div.kreacad_toolbar div.kreacad_toolbar_button.selected {
	background: var(--kreacad_toolbar_selected_color);
}

div.kreacad_toolbar div.kreacad_toolbar_separator {
	background: var(--kreacad_toolbar_separator_color);
	width: 1px;
	height: 28px;
	margin: 5px 8px;
	float: left;
}

/* Version label inside toolbar */

div.kreacad_toolbar_version,
.kreacad_toolbar_version {
	float: right;
	padding: 10px 8px;
	font-size: 11px;
	font-family: monospace;
	opacity: 0.75;
	color: var(--ov_foreground_color);
	cursor: default;
	-webkit-user-select: text;
	user-select: text;
	line-height: 18px;
}

/* Dark theme tweak if needed */
body.ov_dark .kreacad_toolbar_version {
	opacity: 0.85;
	text-shadow: 0 0 4px rgba(160, 180, 255, 0.35), 0 0 1px rgba(255, 255, 255, 0.5);
}

div.pcr-app {
	color: var(--ov_dialog_foreground_color);
	background: var(--ov_dialog_background_color);
}

div.pcr-app input.pcr-result {
	color: var(--ov_foreground_color) !important;
	background: var(--ov_background_color) !important;
}

div.ov_property_table {
	overflow: auto;
}

div.ov_property_table_custom {
	margin-top: 8px;
	padding-top: 8px;
	border-top: 1px solid var(--ov_border_color);
}

div.ov_property_table div.ov_property_table_row {
	overflow: auto;
}

div.ov_property_table div.ov_property_table_row.group {
	padding: 4px 0px;
	white-space: nowrap;
	text-overflow: ellipsis;
	overflow: hidden;
}

div.ov_property_table div.ov_property_table_row.ingroup {
	margin-left: 15px;
}

div.ov_property_table div.ov_property_table_cell {
	padding: 4px 0px;
	white-space: nowrap;
	text-overflow: ellipsis;
	overflow: hidden;
	box-sizing: border-box;
}

div.ov_property_table div.ov_property_table_name {
	width: 49%;
	padding-right: 2%;
	float: left;
}

div.ov_property_table div.ov_property_table_value {
	width: 49%;
	text-align: left;
	float: left;
}

div.ov_property_table div.ov_property_table_button {
	color: var(--ov_button_color);
	cursor: pointer;
}

div.ov_bottom_floating_panel {
	background: var(--ov_background_color);
	border-top: 1px solid var(--ov_border_color);
	width: 100%;
	padding: 30px;
	box-sizing: border-box;
	position: absolute;
	bottom: 0px;
}

div.ov_bottom_floating_panel div.ov_floating_panel_text {
	padding: 3px;
	margin-bottom: 10px;
	float: left;
}

div.ov_bottom_floating_panel div.ov_floating_panel_button {
	width: 120px;
	float: right;
}

div.ov_measure_panel {
	padding: 6px 15px;
	position: absolute;
	border-radius: 30px;
	left: 0px;
	top: 0px;
}

div.ov_measure_panel div.ov_svg_icon {
	color: inherit;
	margin-bottom: 2px;
}

div.ov_measure_panel div.ov_measure_value {
	float: left;
	margin-right: 10px;
}

@media (hover) {

	div.title_right div.header_button:hover {
		color: var(--ov_button_color);
	}

	div.ov_toolbar div.ov_toolbar_button:hover {
		background: var(--ov_hover_color);
	}

}

@media (max-width: 350px),
(max-height: 620px) {

	div.intro_content {
		margin: 0px auto;
	}

	div.intro div.intro_logo {
		display: none;
	}

}

@media (max-width: 800px) {

	div.intro_content {
		width: auto;
	}

	div.main_viewer canvas {
		border: 0px;
		margin: 0px;
	}

	div.ov_dialog {
		max-width: 80%;
	}

	div.ov_progress {
		max-width: 80%;
	}

	div.ov_bottom_floating_panel {
		padding: 10px;
	}

}

#create_mode_iframe {
	display: none;
	border: none;
	position: absolute;
	top: 50px;
	/* Header height */
	left: 0;
	width: 100%;
	height: calc(100% - 50px);
	z-index: 20;
	background: #0f1115;
}

/* Dark themed material dropdown */
.ov_material_dropdown {
	width: 100%;
	padding: 4px 6px;
	background: #1c1f24;
	color: #e2e7ed;
	border: 1px solid #2d333b;
	border-radius: 4px;
	font-size: 12px;
	font-family: inherit;
	outline: none;
	transition: background .15s, border-color .15s, box-shadow .15s;
}

.ov_material_dropdown:hover {
	background: #23272d;
	border-color: #3a434c;
}

.ov_material_dropdown:focus {
	background: #262c33;
	border-color: #4690c7;
	box-shadow: 0 0 0 2px rgba(70, 144, 199, 0.35);
}

.ov_material_dropdown option {
	background: #1c1f24;
	color: #e2e7ed;
}

.ov_property_table .ov_material_dropdown:disabled {
	opacity: .55;
	cursor: not-allowed;
}
